<template>
  <div class="orderList">
    <div class="navList">
      <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
        <van-tab v-for="(item,index) in orderClassifyList" :key="index" :title="item.label"></van-tab>
      </van-tabs>
      <div class="mid">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in list" :key="index">
              <div class="item-top flex" @click="toDetail(item.id)">
                <div class="No flex-2">:{{item.order_no}}</div>
                <div class="status" :style="item.order_status == 1 || item.order_status == 3?'color:#eb5252':'color:#999'">{{$utils.getText(item.order_status)}}</div>
              </div>

              <div class="orderInfo" @click="toDetail(item.id)">
                <div class="verificationCode">:{{item.write_off_code}}</div>
                <div class="price">{{item.actual_amount.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div class="btn btnPay" v-if="item.order_status  == 1" @click="handle(item,3)"></div>
                <div class="btn" @click="toDetail(item.id)"></div>
                <div class="btn" v-if="item.order_status  == 11" @click="handle(item,5)"></div>
                <div class="btn" v-if="item.order_status  == 1" @click="handle(item,6)"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog } from "vant";
// import {
//   getActivityOrderList,
//   weixinPay,
//   deleteActivityOrder,
//   cancelActivityOrder
// } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      orderClassifyList: [
        { label: "", value: "" },
        { label: "", value: "1" },
        { label: "", value: "2" },
        { label: "", value: "5" }
      ],
      mescroll: null,
      list: [],
      orderId: "",
      orderNo: ""
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.orderClassifyList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toDetail(id) {
      this.$router.push({ name: "activityOrderDetail", query: { id: id } });
    },
    handle(item, type) {
      this.orderId = item.id;
      this.orderNo = item.order_no;
      let that = this;
      switch (type) {
        case 3:
          //
          Dialog.confirm({
            title: "",
            message: ""
          })
            .then(() => {
              // on confirm
              that.makePreOrder();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 5:
          //
          Dialog.confirm({
            title: "",
            message: ""
          })
            .then(() => {
              // on confirm
              that.delete();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 6:
          //
          Dialog.confirm({
            title: "",
            message: ""
          })
            .then(() => {
              // on confirm
              that.cancel();
            })
            .catch(() => {
              // on cancel
            });
          break;
        default:
          console.log(type);
      }
    },
    delete() {
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      deleteActivityOrder({
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        activity_order_id: this.orderId
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("");
            this.mescroll.resetUpScroll();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "",
            type: "cancel"
          });
        });
    },
    cancel() {
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      cancelActivityOrder({
        activity_order_id: this.orderId
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("");
            this.mescroll.resetUpScroll();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "",
            type: "cancel"
          });
        });
    },
    makePreOrder() {
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      weixinPay({
        out_trade_no: this.orderNo,
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        oauth_openid:
          sessionStorage.getItem("openId") || "ofDvU6RWRq_iQobXT-vyJx1yJ8Dw"
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            if (!res.data.appId) {
              this.$toast("", "middle");
              this.mescroll.resetUpScroll();
              // this.$router.push({
              //   name: "payResult",
              //   query: { id: this.detail.id }
              // });
            } else {
              this.weChatPay(
                res.data.appId,
                res.data.timeStamp,
                res.data.nonceStr,
                res.data.packageValue,
                res.data.signType,
                res.data.sign
              );
            }
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          loading.clear();
          console.log(err);
          this.$toast({
            text: "",
            type: "cancel"
          });
        });
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //
            timeStamp: timeStamp, //1970
            nonceStr: nonceStr, //
            package: packageValue,
            signType: signType, //
            paySign: sign //
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("", "middle");
              $this.mescroll.resetUpScroll();
              // setTimeout(() => {
              //   $this.$router.replace({
              //     name: "payResult",
              //     query: { id: $this.detail.id }
              //   });
              // }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.$toast("", "middle");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.$toast("", "middle");
            }
            // ,res.err_msg    ok
          }
        );
      }
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        pageNum: pageNum,
        pageSize: pageSize,
        order_status: this.activeValue
      };
      getActivityOrderList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //vuemountedmescroll,id
        up: {
          callback: this.upCallback, //
          //,
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //8,10
          empty: {
            //
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: ""
            //						  	btntext : " >" ,
            //						  	btnClick : function() {
            //						  		alert("");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">--  --</p>',
          lazyLoad: {
            use: true // ,false
          }
        }
      });
    },
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback: function(page) {
      //
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //,empty

          // (clearIdclearEmptyId)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.rows) {
            curPageData.rows.map(item => {
              $this.list.push(item);
            });
          }
          //,;
          //mescroll,,;,;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //():  totalSize
          $this.mescroll.endBySize(
            curPageData.rows ? curPageData.rows.length : 0,
            curPageData.recordCount
          ); //(, )
        },
        function() {
          //,;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "";
    this.active = this.$route.query.index || 0;
    this.activeValue = this.orderClassifyList[this.active].value;
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.orderList {
  .navList {
    margin-bottom: 10px;
  }
  .mid {
    height: calc(100vh - 44px);
  }
  .list {
    border-top: 1pt solid #efefef;
    .item {
      padding: 15px 10px;
      border-bottom: 1pt solid #efefef;
      background: #fff;
      .flex {
        display: flex;
        .flex-1 {
          flex: 1;
        }
        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .item-top {
        font-size: 15px;
        line-height: 20px;
        .No {
          color: #333;
        }
      }
      .item-goods {
        margin: 15px 0;
        .goods-pic {
          width: 100px;
          height: 100px;
          margin-right: 10px;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 5px 0 10px;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
      .orderInfo {
        font-size: 15px;
        .verificationCode {
          font-size: 16px;
          line-height: 30px;
          padding: 10px 0;
        }
        .price {
          font-size: 14px;
          text-align: right;
          color: #666;
        }
      }
      .item-bottom {
        text-align: center;
        overflow: hidden;
        padding: 15px 0;
        .btn {
          width: 80px;
          color: #666;
          border: 1pt solid #e5e5e5;
          float: right;
          height: 30px;
          line-height: 30px;
          border-radius: 5px;
          font-size: 13px;
          margin-left: 5px;
        }
        .btnPay {
          background: #fff;
          color: $base-color;
          border: 1pt solid $base-color;
        }
      }
    }
  }
}
</style>
